.how{
    padding: 120px 12px;
}

.header{
    margin: 0 auto;
    width: fit-content;
}

.headerIcon{
    margin: 0 auto;
}


.header h3{
    text-align: center;
    font-size: 32px;
    margin: 16px 0;
}

.header p {
    width: 60%;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 36px;
}

.howItems{
    position: relative;
    display: flex;
}

.box{
    box-sizing: border-box;
    overflow: hidden;
    width: 33.33%;
    min-height: 300px;
    padding: 24px;

}

.box img{
    width: 100%;
}

.box h3{
    text-align: center;
}

.box p{
    text-align: center;
}