.burgerLabel {
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 50px;
  cursor: pointer;
  margin-right: 12px;
  height: 50px;
}

.burgerLabel span {
  background: rgb(0, 0, 0);
  border-radius: 10px;
  height: 7px;
  margin: 4px 0;
  transition: 0.4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

span:nth-of-type(1) {
  width: 50%;
}

span:nth-of-type(2) {
  width: 100%;
}

span:nth-of-type(3) {
  width: 75%;
}


